import React, {useState} from 'react';
import './index.css'
import {Link} from 'react-router-dom'
const Index = () => {
  const [flag, setFlag] = useState(true)
  const [show, changeShow] = useState(false)
  const onBottom = () => {
    const ele = document.body
    ele.scrollIntoView(false)
    if (flag) {
      showWords(0)
    }
  }
  window.addEventListener("load", function(event) {
    document.getElementById("container").style.visibility = "visible"
    document.getElementById("loading").style.display = "none"
  });
  const showWords = (count) => {
    setFlag(false)
    if (count < 3) {
      let num = count
      const s1 = document.getElementById("sentence1")
      const s2 = document.getElementById("sentence2")
      const s3 = document.getElementById("sentence3")
      const s4 = document.getElementById("sentence4")
      const eleList = [s1, s2, s3, s4]
      eleList[num].style.visibility = "visible"
      setTimeout(function() {
        if(num - 1 >= 0) {
          eleList[num - 1].style.visibility = "hidden"
        }
        eleList[num].style.color = "rgba(253, 245, 211, 0.45)"
        if(num + 1 < 4) {
          eleList[num + 1].style.visibility = "visible"
        }
        // 
        if(num === 2) {
          changeShow(true)
        }
        showWords(num + 1)
      }, 5000)
    }
  }
  return (
    <div>
      <div id="loading">loading</div>
      <div id="container" className="index-container" onClick={onBottom}>
        <div className="index-title">2019<span className="index-flag">#TAG#</span></div>
        <div className="index-words">
          <div id="sentence1">
            <span style={{ fontSize: "6.5vw" }}></span>
            <p style={{ fontSize: "6.3vw" }}>NCUHOMES GACHAPON</p>
          </div>
          <div id="sentence2">
             ()
            <p><span style={{ fontSize: "8vw", marginLeft: 20 }}>""</span></p>
          </div>
          <div id="sentence3">
            <p>2019</p>
            <p></p>
            <p><span style={{ fontSize: "8vw" }}>""</span></p>
            <p></p>
            <p></p>
            <p>...</p>
          </div>
          <div id="sentence4">
            <p></p>
            <p></p>
            <p style={{ fontSize: "6.5vw" }}>...</p>
          </div>
          <Link to="#sentence1" id="test" style={{visible: "none"}}></Link>
        </div>
        {show ? (<Link to="/papers" className="index-get"></Link>) : <div></div>}
      </div>
    </div>
  )
}
export default Index;